<template>
  <el-container style="width:100%;height:100%;">
    <el-row :gutter="5" style="width:100%;height:100%;" justify="center">
      <el-col :span="16" style="height:100%;padding:40px;">
        <el-card shadow="hover" style="height:100%;">
          <el-row></el-row>
          <el-divider></el-divider>
          <el-row :gutter="5" justify="center">
            <div
              style="width:100%;height:100%;display:flex;justify-content:center;align-items:center;"
            >
              <el-button
                type="primary"
                class="trans-item"
                @click="startTransaction('Inquiry',$event)"
              ></el-button>
              <el-button
                type="primary"
                class="trans-item"
                @click="startTransaction('Deposit',$event)"
              ></el-button>
            </div>
          </el-row>
          <el-row :gutter="5" justify="center">
            <div
              style="width:100%;height:100%;display:flex;justify-content:center;align-items:center;"
            >
              <el-button
                type="primary"
                class="trans-item"
                @click="startTransaction('Withdrawal',$event)"
              ></el-button>
              <el-button
                type="primary"
                class="trans-item"
                @click="startTransaction('Transfer',$event)"
              ></el-button>
            </div>
          </el-row>
          <el-row :gutter="5" justify="center">
            <div
              style="width:100%;height:100%;display:flex;justify-content:center;align-items:center;"
            >
              <el-button
                type="primary"
                class="trans-item"
                @click="startTransaction('Loan',$event)"
              ></el-button>
              <el-button
                type="primary"
                class="trans-item"
                @click="startTransaction('Payback',$event)"
              ></el-button>
            </div>
          </el-row>
          <el-row :gutter="5" justify="center">
            <div
              style="width:100%;height:100%;display:flex;justify-content:center;align-items:center;"
            >
              <el-button
                type="primary"
                class="trans-item"
                @click="startTransaction('ChangePIN',$event)"
              >PIN</el-button>
              <el-button type="primary" class="trans-item" disabled>...</el-button>
            </div>
          </el-row>
        </el-card>
      </el-col>
      <el-col :span="8" style="height:100%;padding:40px;">
        <el-card shadow="hover" style="height:100%;">
          <el-row></el-row>
          <el-divider></el-divider>
          <el-row :gutter="5">
            <el-col :span="12"></el-col>
            <el-col :span="12">{{name}}</el-col>
          </el-row>
          <el-row :gutter="5">
            <el-col :span="12"></el-col>
            <el-col :span="12">{{balances}}</el-col>
          </el-row>
          <el-row :gutter="5">
            <el-col :span="12"></el-col>
            <el-col :span="12">
              {{credits}}
              <el-tag v-if="credits>=90" type="success" size="mini"></el-tag>
              <el-tag v-else-if="credits>=70" type="primary" size="mini"></el-tag>
              <el-tag v-else type="danger" size="mini"></el-tag>
            </el-col>
          </el-row>
          <div style="display:flex;padding:20px;justify-content:center;">
            <el-button type="info" icon="el-icon-upload2" round @click.native="ejectCard"></el-button>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </el-container>
</template>
<script>
/**
 * 
 *
 */
import {mapState} from 'vuex';

export default {
  computed: {
    ...mapState({
      name: state => state.account.name,
      number: state => state.account.number,
      balances: state => state.account.balances,
      credits: state => state.account.credits
    })
  },
  methods: {
    /**
     * 
     * @param {string} type 
     */
    startTransaction(type) {
      let route = "";
      switch (type) {
        case "Inquiry":
          route = "inquiry";
          break;
        case "Deposit":
          route = "deposit";
          break;
        case "Withdrawal":
          route = "withdrawal";
          break;
        case "Transfer":
          route = "transfer";
          break;
        case "ChangePIN":
          route = "change-pin";
          break;
        case "Loan":
          route = "loan";
          break;
        case "Payback":
          route = "payback";
          break;
        default:
          alert("");
          return;
      }
      this.$router.push({ name: route });
    },
    /**
     * 
     */
    ejectCard() {
      this.$store.dispatch("ejectCard");
      this.$message.info("");
      this.$router.replace({ name: "idle" });
    }
  }
};
</script>
<style scoped>
.trans-item {
  width: 220px;
  height: 70px;
  margin: 10px;
}
</style>